<template>
    <div class="processed-order">

        <ul class="cell-content" v-infinite-scroll="requestData" infinite-scroll-disabled="cellLoading" infinite-scroll-distance="0" infinite-scroll-immediate-check="false">
            <li v-for="dataItem in goodsArr" :key="dataItem.id">
                <order-cell  class="cell" :cell-data="dataItem"></order-cell>
            </li>
        </ul>

        <empty-bg :msg="emptyMsg" v-show="showEmpty"></empty-bg>
    </div>
</template>

<script>
import { mapState } from "vuex";
import { formatDate } from "../../../../common/js/date.js";
import EmptyBg from "../../../popupBox/emptyBg.vue";
import orderCell from "./orderCell.vue";
import Vue from "vue";
import { InfiniteScroll } from "mint-ui";
Vue.use(InfiniteScroll);

export default {
  name: "processed-order",
  data() {
    return {
      goodsArr: null,
      showEmpty: false,
      emptyMsg: "没有已处理的订单~",
      memoryScroll: 0,
      cellLoading: true,
      curPage: 0,
      pageNum: 10,
      existIdArr: [] //保存已经显示的订单id
    };
  },
  components: {
    EmptyBg,
    orderCell
  },
  mounted() {
    this.showEmpty = false;
    if(this.$parent){
        this.$parent.processedData=[];
        this.goodsArr=this.$parent.processedData;
    }
    this.requestData(false);
  },
  activated() {
    document.body.scrollTop=this.memoryScroll;
  },
  beforeRouteLeave(to, from, next) {
    this.memoryScroll=document.body.scrollTop;
    next();
  },
  methods: {
    requestData(infinite = true) {
      if (infinite && this.page == 0) return;
      if (infinite && this.cellLoading) return;
      this.cellLoading = true;
      this.$http
        .post(this.path + `php/Route.php`, {
          type:'agentgoods',
          aid:this.userid,
          page:this.curPage,
          tp:1
        })
        .then(response => {
          let serverData = response.body.arr;
          if (!serverData || serverData.length == 0) {
            //数据为空
            this.cellLoading = true;
            if (this.curPage == 0) this.showEmpty = true;
          } else {
            //有数据
            if (serverData.length % 10 == 0) {
              //满一页数据
              this.curPage++;
              this.cellLoading = false;
            } else {
              //不满一页
              this.cellLoading = true;
            }
            let detailsArr = this.analysisData(serverData);
            for (let i = 0; i < detailsArr.length; i++) {
              let orderid = detailsArr[i].id;
              if (this.existIdArr.indexOf(orderid) >= 0) continue;
              this.goodsArr.push(detailsArr[i]);
              this.existIdArr.push(orderid);
            }
          }
        });
    },
    analysisData(data) {
      let detailsArr = [];
      data.forEach((val, index, arr) => {
        let tempDate = new Date(val.buytime * 1000);
        let happenTime = formatDate(tempDate, "yyyy-MM-dd hh:mm");
        detailsArr.push({
          id: val.buyid,
          username: val.username,
          userTel: val.phone,
          time: happenTime,
          goodsname: val.gname,
          price: val.price,
          state: val.orderstate //0 未处理  3退款中   4已经退款  5 已经处理
        });
      });
      return detailsArr;
    },
  },
  computed: {
    ...mapState(["path", "userid", "role"])
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.processed-order {
    // height: 100%;

    .cell {
        box-sizing: border-box;
        margin: 0 0.06rem;
        // box-shadow: 0px 1px 1px rgba(80, 80, 80, 0.1);
        border-radius: 0.02rem;
        background: #fff;
        margin-bottom: 0.08rem;
    }

    .empty-bg{
      height 75vh;
    }
}
</style>



