<template>
    <div class="order-cell">
        <div class="header">
            <span class="iconfont icon-riliriqi"><span class="time">{{cellData.time}}</span></span>
            <span class="order-no">编号：{{cellData.id}}</span>
        </div>
        <div class="content">
            <div class="info user-info">
                <span class="info-title">(商家)：</span>
                <span class="name">{{cellData.username}} </span>,
                <a :href="'tel:'+cellData.userTel"><span class="iconfont icon-phone"></span><span class="extra">{{cellData.userTel}}</span></a>
            </div>
            <div class="info goods-info">
                <span class="info-title">(商品)：</span>
                <span class="name">{{cellData.goodsname}} </span>,
                <span class="extra">&yen;{{cellData.price}}</span>
            </div>
        </div>
        <div class="state">
            <span class="click-item" @click="markToProcessed" v-if="cellData.state.toString()=='0'">标记为已处理</span>
            <span class="tip" v-else-if="cellData.state.toString()=='3'">退款中</span>
            <span class="tip" v-else-if="cellData.state.toString()=='4'">已退款</span>
            <span class="tip" v-else-if="cellData.state.toString()=='5'">已处理</span>
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "order-cell",
  data() {
    return {};
  },
  props: {
    cellData: {
      type: Object,
      default: function() {
        return {
          id: "",
          time: "",
          username: "",
          userTel: "",
          state: "",
          goodsname: "",
          price: 0
        };
      }
    }
  },
  components: {},
  activated() {},
  mounted() {},
  computed: {
    ...mapState(["openid", "path", "role"])
  },
  methods: {
      markToProcessed(){
          if(!this.$parent||!this.$parent.$parent) return;
          this.$parent.$parent.markToProcessed(this.cellData);
      }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
@import '../../../../common/css/mixin.styl';

.order-cell {
    padding: 0 0.1rem;

    .header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 0.26rem;
        color: #303030;

        .icon-riliriqi {
            font-size: 0.16rem;

            .time {
                margin-left: 0.05rem;
                font-size: 0.12rem;
                font-weight: 600;
            }
        }

        .order-no {
            font-size: 0.12rem;
            letter-spacing: 0.01rem;
        }
    }

    .content {
        min-height: 0.48rem;
        display: flex;
        flex-direction: column;
        justify-content: center;

        .info {
            font-size: 0.13rem;
            color: #303030;

            .info-title {
                letter-spacing: 0.01rem;
            }

            &.user-info {
                .icon-phone {
                    font-size: 0.14rem;
                    color: #FD8709;
                }

                .extra {
                    color: #FD8709;
                }
            }

            &.goods-info {
                margin-top: 0.05rem;

                .extra {
                    color: #FD8709;
                }
            }
        }
    }

    .state {
        border-top-1px(#e3e3e3);
        height: 0.3rem;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        font-size: 0.12rem;
        letter-spacing: 0.01rem;

        .click-item {
            color: #303030;
            padding: 0.02rem 0.05rem;
            border-radius: 0.03rem;
            border: 1px #999 solid;
        }

        .tip {
            color: #707070;
            font-size: .12rem;
            padding-right: 0.03rem;
        }
    }
}
</style>



